.App {
  font-family: sans-serif;
  text-align: center;
  background-color: beige;
  color: firebrick;
  font-size: 25px;
}
.App-header {
  background-color: beige;
  position: relative;
}
.App-text {
  color: firebrick;
}
.button {
  background-color: rgba(51, 51, 51, 0.05);
  border-radius: 8px;
  border-width: 10;
  color: #333333;
  cursor: pointer;
  display: inline-block;
  font-family: "Haas Grot Text R Web", "Helvetica Neue", Helvetica, Arial,
    sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  list-style: none;
  margin: 16px;
  padding: 010px 22px;
  text-align: center;
  transition: all 200ms;
  vertical-align: baseline;
  white-space: nowrap;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  position: relative;
}

@media (min-width: 768px) {
  .button-53 {
    font-size: 1.25rem;
  }
}

